import React from 'react';
import styled from 'styled-components';
import DateTime from 'luxon/src/datetime';

const Code = styled.code`
  display: block;
  background-color: #f1f1f1;
  font-size: 13px;
  margin-bottom: 3px;
`;

const DocumentationWrapper = styled.div`
  text-align: center;
`;

export default ({ dumpUrl, lastPingDate }) => (
  <DocumentationWrapper>
    <Code>GET <a href={ dumpUrl } target="_blank">{ dumpUrl }</a></Code>
    <Code>Proxies last pinged {lastPingDate ? Math.round(DateTime.now().diff(DateTime.fromISO(lastPingDate), 'minutes').minutes) : '?'} minutes ago</Code>
  </DocumentationWrapper>
);
