import React from 'react';
import { WorldMap } from "react-svg-worldmap"
import styled from 'styled-components';

const H3 = styled.h3`
  font-size: 18px;
  font-style: italic;
  text-align: center;
  margin-top: 30px;
`;

const P = styled.p`
  text-align: center;
`;

const getTotalProxyCount = (countryMetadata = {}) => {
  let totalProxyCount = 0;

  Object.values(countryMetadata.proxy_count).map(count => totalProxyCount+=parseInt(count));

  return totalProxyCount;
};

const getTotalCountryCount = countryMetadata => {
  let totalCountryCount = 0;

  Object.keys(countryMetadata.proxy_count).map(country => totalCountryCount++);

  return totalCountryCount;
}

export default ({ countryMetadata = {} }) => (
  <div style={{width: "100%"}}>
    <H3>Stats</H3>
    <P>{getTotalProxyCount(countryMetadata)} proxies in {getTotalCountryCount(countryMetadata)} countries</P>
    <WorldMap color="red" title="Average ping" size="xl" valueSuffix="ms" data={Object.keys(countryMetadata.average_ping).map(country => ({
      country: country.toLowerCase(),
      value: Math.round(countryMetadata.average_ping[country])
    }))} />
    <WorldMap color="green" title="Proxy count" size="xl" data={Object.keys(countryMetadata.proxy_count).map(country => ({
      country: country.toLowerCase(),
      value: Math.round(countryMetadata.proxy_count[country])
    }))} />
  </div>
);
